<template>
  <div class="wrapper">
    <div class="info-box">
      <div class="head">
        <span class="gradient-title">{{$t('calc.pp')}}</span>
      </div>
      <div class="des">
        <div>
          <span class="label">{{$t('calc.bp')}}</span>
          <span class="value">{{userTotalPower - teamPower}}</span>
        </div>
        <div>
          <span class="label">{{$t('calc.tp')}}</span>
          <span class="value">{{teamPower}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { commonMixin } from '@/utils/mixins'
export default {
  mixins: [ commonMixin ],
  data () {
    return {
      showStatusTip: false,
      teamPower: '0.000',
      userTotalPower: '0.000'
    }
  },
  created() {
    if (this.address) {
      this.initData()
    }
  },
  methods: {
    initData () {
      this.getUserTotalPower()
      this.getTeamPower()
    },
    getUserTotalPower () {
      this.$ald.balanceOf(this.address).then(res => {
        this.userTotalPower = Number(res.toString()/Math.pow(10, 18)).toFixed(4)
      })
    },
    getTeamPower () {
      this.$ald.teamPower(this.address).then(res => {
        this.teamPower = Number(res.toString()/Math.pow(10, 18)).toFixed(4)
      })
    }
  },
  watch: {
    address (val) {
      if (val) {
        this.initData()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.info-box {
  margin: 12px 14px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.08);
  .head {
    line-height: 45px;
    padding-left: 12px;
  }
  .des {
    padding: 0 12px;
    border-top: 1px solid #DEDEDE;
    div {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 47px;
    }
    .label {
      color: #666666;
      font-size: 15px;
      font-weight: 500;
    }
    .value {
      color: #111111;
      font-size: 15px;
      font-weight: bolder;
    }
  }
}
</style>
